@font-face {
  font-family: "Poppins Medium";
  font-style: normal;
  font-weight: normal;
  src: url(/static/media/Poppins-Medium.f61a4eb2.ttf);
}
@font-face {
  font-family: "Poppins Regular";
  font-style: normal;
  font-weight: normal;
  src: url(/static/media/Poppins-Regular.8b6af8e5.ttf);
}
@font-face {
  font-family: "Poppins SemiBold";
  font-style: normal;
  font-weight: normal;
  src: url(/static/media/Poppins-SemiBold.4cdacb8f.ttf);
}
* {
  outline: none;
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  overflow-y: overlay;
  font-family: "Poppins Regular";
  background: var(--color-bg);
  padding: 0;

}
::-webkit-scrollbar {
  display: none;
}
.btn {
  border-radius: 45px;
  padding: 14px 0;
  width: 130px;
  cursor: pointer;
  transition: 0.2s;
  font-family: "Poppins SemiBold";
}
.primary-btn {
  color: var(--white);
  border: 2px solid linen;
  font-size: 12px;
  background-color: #ee1379;
}
.primary-btn:hover {
  color: aliceblue;
  border: 2px solid #fc11ec;
  background-color: #823e71;
} 
:root{
  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}
html{
  scroll-behavior: smooth;
}
.container{
  width: 75%;
  width: var(--container-width-lg);
  margin: 0 auto;
}

.profile-container{
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    height: 10%;
    min-height: -webkit-fit-content;
    min-height: -moz-fit-content;
    min-height: fit-content;
}
.profile-details-name{
    font-size: 24px;
    font-family: "Poppins";
}
.profile-details-role{
    display: flex;
    flex-direction: column;
    margin: 14px 0 24px 0;
}
.profile-details-role h1{
    font-size: 10px;
    font-family: Verdana;
}
span.primary-text > h2 > p{
    color: (--white);
    height: -webkit-max-content;
    height: max-content;
    word-wrap: break-word;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.profile-role-tagline{
    font-size: 18px;
    margin: 5px 0 0 0;
    font-family: Poppins Light;
}


.profile-picture{
    display: flex;
    margin: auto;
    align-items: center;
    justify-content:center;
    box-shadow: 0 5px 0px 0px var(--white);
    border-radius: 50%;
    height: 380px;
    width: 380px;
}
.profile-picture-background{
    height: 92%;
    width: 92%;
    background-image: url(/static/media/IMG-9716.dd9e16d0.jpg);
    background-size: cover;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center;
}
.colz .colz-icon i{
    padding: 8px;
    padding-left: 0px;
}
.colz .colz-icon i:hover{
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
.profile-details{
    width: 100% !important;
}

.profile-parent{
    width: 100%
}
.footer-container{
    height: 17.8%;
    width: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
}
.footer-parent{
    height: 90%;
    width: 100%;
    max-width: 1920px;
}
.footer-parent img{
    max-width: 100%;
    height: 130px;
}

p{
    padding: 15px;
    margin-left: 5%;
    font: 20px;
}

h1{
    text-align: center;
    padding-top: 55px;
    padding-bottom: 20px;
    font-family: Poppins SemiBold;
}
.about-container{
    background-color: #f2aeb1;
}
.about-me-img{
    display: flex;
    align-items: center;
    background-size: cover;
    padding: 45px;
    
}

div.about-me-img > img {
    width: 35%;
    height: 40%;
    border-radius: 10px;
    border-style: solid;
    border-width: 5px;
    /* border-radius: 5px; */
    /* border-color: #6a083e; */
    border-color: white;
}
.home-container{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 670px;
    background-image: url(/static/media/IMG-1311.debd202c.jpg);
}
/* .home-container.contact-options{
    width: var(--container-width-lg);
    margin: 0 auto;
    padding: 30% ;
} */
.container.contact-container{
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    grid-gap: 12%;
    gap: 12%;
}
div.contact-options{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}
.contact-option{
    background-color: rgb(161, 10, 111) ;
    /* padding: 1.2rem;
    border-radius: 1.2rem; */
    text-align: center;
    /* border: 1px solid transparent; */
    transition: var(--transition);
    color: white;
    border: 2px solid linen;
}
a{
    color: white;
    font-size: 25px;
}
.contact-option:hover{
    background-color: #823e71;
}
/* article.contact-option{
    position: absolute;
    left: auto;
    border-radius: 5px;
    padding: 70px;
} */
.contact-option-icon{
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
    padding: 4px;
}
.contact-option a{
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 00.7rem;
}
form{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    padding: auto;
    
}
input, textarea{
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid white;
    resize: none;
}
 .btn-primary{
    background-color: rgb(161, 10, 111);
    border: 2px solid white;
    padding-bottom: 15px;
    align-content: center;
  
}
.btn-primary:hover{
    background-color: #823e71;
    border: 2px solid white;
}
.footer-end-container{
    background-color: rgb(161, 10, 111);
}
.footer-text{
    color: white;
    text-align-last: right;
    text-align: justify;
    font-size: 20px;
}

